import React, { useState } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import TextError from "../../Component/TextError/TextError";
import { useSelector, useDispatch } from "react-redux";
import { errorFunction } from "../../Component/Alert";
import {
  createUserGroup,
  updateUserGroup,
} from "../../Redux/UserGroup/thunk";
import { checkRedundantUserGroup } from "../../utils/redundant/userGroup";
import Button from "../../Component/Button/Button";

import Permission from "./Permission";
import { useHistory } from "react-router-dom";
const CreateUserGroup = () => {
  const history = useHistory();
  // props
  const userGroup = useSelector((state) => state.userGroup.userGroup);
  const edit = useSelector((state) => state.userGroup.edit);
  const dispatch = useDispatch();
  //state for disabling the submit button
  const [lock, setLock] = useState(false);
  //initial state of the form
  const initialState = {
    name: edit ? userGroup?.name : "",
    permissions: edit ? (userGroup ? userGroup?.permissions : []) : [],
    is_active: edit ? userGroup.is_active : true,
    remarks: "",
  };

  // permission
  const [selectedPermissions, setSelectedPermissions] = useState([]);
  const [holdPermissions, setHoldPermissions] = useState([]);
  //validation rules of the form
  const validationSchema = Yup.object().shape({
    name: Yup.string()
      .required("Role is required.")
      .min(1, "Role must be at least 1 characters.")
      .max(50, "Role must be 50 characters."),
    is_active: Yup.bool(),
    remarks:
      edit &&
      Yup.string()
        .required("Remarks is required.")
        .min(1, "Remarks Name must be at least 1 characters."),
  });
  const onSubmit = (values) => {
    const updatedPermissions = selectedPermissions.map(
      (permissions) => permissions.id
    );
    setLock(true);
    if (edit) {
      const id = userGroup.id;
      dispatch(
        updateUserGroup(id, history, {
          ...values,
          permissions: updatedPermissions,
        })
      );
    } else {
      dispatch(
        createUserGroup({ ...values, permissions: updatedPermissions },history)
      );
    }
    setLock(false);
  };
  // function which checks whether the bank is added already or not
  const handleChange = async (e) => {
    if (edit && e.target.value !== userGroup?.name) {
      if (e.target.value.trim() && e.target.value !== "") {
        const result = await checkRedundantUserGroup(e);
        result
          ? errorFunction("User Group has already been added ") || setLock(true)
          : setLock(false);
      }
    }
    if (!edit && e.target.value.trim() && e.target.value !== "") {
      const result = await checkRedundantUserGroup(e);
      result
        ? errorFunction("User Group  with this name is already added. ") ||
          setLock(true)
        : setLock(false);
    }
  };

  return (
    <>
      <div className="row mt-3">
        <div className="col-12 ">
          <div className="registration-process ">
            <Formik
              enableReinitialize={true}
              initialValues={initialState}
              validationSchema={validationSchema}
              onSubmit={onSubmit}
            >
              {({ setFieldValue, values, dirty }) => {
                return (
                  <Form autoComplete="off">
                    <div className="form-group row  justify-content-center">
                      <div className="col-10">
                        <label htmlFor="name" className="fw-bold float-start">
                          Name
                          <strong className="text-danger">*</strong>
                        </label>
                        <Field
                          type="text"
                          name="name"
                          className="form-control "
                          placeholder="Role Name"
                          onChange={(e) => {
                            setFieldValue("name", e.target.value);
                            handleChange(e);
                          }}
                          autoFocus
                        />
                        <ErrorMessage name="name" component={TextError} />
                      </div>
                    </div>
                    <div className="form-group row my-4 mt-2 justify-content-center">
                      <div className="col-10">
                        <Field
                          type="checkbox"
                          name="is_active"
                          id="is_active"
                          className="filled-in chk-col-blue"
                        />
                        <label htmlFor="is_active" className="fw-bold mx-1">
                          {" "}
                          Active
                        </label>
                      </div>
                    </div>
                    <Permission
                      selectedPermissions={selectedPermissions}
                      setSelectedPermissions={setSelectedPermissions}
                      holdPermissions={holdPermissions}
                      setHoldPermissions={setHoldPermissions}
                    />

                    {edit && (
                      <div
                        className="form-group row me-5 "
                        style={{ marginLeft: 80 }}
                      >
                        <div className="col-4">
                          <label
                            htmlFor="remarks"
                            className="fw-bold float-start"
                          >
                            Remarks
                          </label>
                          <textarea
                            type="text"
                            name="remarks"
                            onChange={(e) => {
                              setFieldValue("remarks", e.target.value.trim());
                            }}
                            className="form-control"
                            placeholder="Enter remarks"
                          />
                          <ErrorMessage name="remarks" component={TextError} />
                        </div>
                      </div>
                    )}
                    <div className="form-group text-center">
                      {!edit ? (
                        <Button
                          type={"btn"}
                          className={"btn btn-primary"}
                          disabled={lock || !dirty}
                          title={"Create"}
                          content={"Add"}
                        />
                      ) : (
                        <Button
                          type={"btn"}
                          className={"btn btn-primary "}
                          disabled={lock}
                          title={"Update"}
                          content={"Update"}
                        />
                      )}
                    </div>
                  </Form>
                );
              }}
            </Formik>
          </div>
        </div>
      </div>
    </>
  );
};
export default CreateUserGroup;
