import * as API from "./api";
import * as action from "./action";
import { errorFunction, successFunction } from "../../Component/Alert";
//get  User Role
export const getUserGroup = (postsPerPage) => async (dispatch) => {
  try {
    dispatch(action.loadingAction());
    const { data } = await API.getUserGroup(postsPerPage);
    dispatch(action.getUserGroupSuccessAction(data));
  } catch (error) {
    dispatch(action.getUserGroupFailAction(error));
  }
};
// get all UserRole
export const getAllUserGroup = () => async (dispatch) => {
  try {
    dispatch(action.loadingAction());
    const { data } = await API.getAllUserGroup();
    dispatch(action.getAllUserGroupSuccessAction(data));
  } catch (error) {
    dispatch(action.getAllUserGroupFailAction(error));
    errorFunction(error);
  }
};
