//get particular page
export const getParticularPage =
  ({ number, postsPerPage }) =>
  async (dispatch) => {
    try {
      dispatch(action.loadingAction());
      const { data } = await API.getPageUserGroup(number, postsPerPage);
      dispatch(action.getUserGroupSuccessAction(data));
    } catch (error) {
      dispatch(action.getUserGroupFailAction(error));
      errorFunction(error);
    }
  };
export const createUserGroup =
  ({ name, is_active, permissions }, history) =>
  async (dispatch) => {
    try {
      const body = JSON.stringify({ name, is_active, permissions });
      dispatch(action.loadingAction());
      const { data } = await API.createUserGroup(body);
      successFunction("User Group Added Successfully");
      history.push("/user-group");
      dispatch(getParticularPage({ number: 1, postsPerPage: 10 }));
    } catch (error) {
      dispatch(action.createUserGroupFailAction(error));
      errorFunction("Failed to create User Group.");
    }
  };

//update
export const updateUserGroup = (id, history, values) => async (dispatch) => {
  const { name, permissions, is_active, remarks } = values;

  try {
    const body = JSON.stringify({ name, permissions, is_active, remarks });
    dispatch(action.loadingAction());
    const { data } = await API.updateUserGroup(id, body);
    // await dispatch(action.updateUserRoleSuccessAction(data));
    successFunction("User Group Updated Successfully ");
    dispatch(getParticularPage({ number: 1, postsPerPage: 10 }));
    history.push("/user-group");
  } catch (error) {
    dispatch(action.updateUserGroupFailAction(error));
    errorFunction("Failed to Update User Group");
  }
};

//get All permission
export const getAllPermission = () => async (dispatch) => {
  try {
    dispatch(action.loadingAction());
    const { data } = await API.getAllPermission();
    if (data.count === 0) {
      successFunction("No permissions available");
    }
    dispatch(action.getAllUserGroupPermissionSuccess(data));
  } catch (error) {
    dispatch(action.getAllUserGroupPermissionFail(error));
  }
};
