//handle Search
export const handleSearch = (search, postsPerPage) => async (dispatch) => {
  try {
    dispatch(action.loadingAction());
    const { data } = await API.handleSearch(search, postsPerPage);
    dispatch(action.getUserGroupSuccessAction(data));
  } catch (error) {
    dispatch(action.getUserGroupFailAction(error));
    errorFunction(error);
  }
};
// specific permissions
export const getSpecificUserPermissions = (id) => async (dispatch) => {
  try {
    dispatch(action.loadingAction());
    const { data } = await API.getSpecificPermissions(id);
    if (data.count === 0) {
      dispatch(successFunction("No permissions available "));
    }
    dispatch(action.getAllUserGroupPermissionSuccess(data));
  } catch (error) {
    dispatch(action.getAllUserGroupPermissionFail(error));
  }
};
