import axiosInstance from "../../utils/axios";

//obtaining the paginated data
export const getUserGroup = (postsPerPage) =>
  axiosInstance.get(
    `api/v1/group-app/group?offset=0&limit=${postsPerPage}&ordering=-id`
  );

//obtaining all data
export const getAllUserGroup = () =>
  axiosInstance.get(`api/v1/group-app/group`);

//obtaining the previous page data from paginated data
export const getPrevious = (previous) => axiosInstance.get(previous);

//obtaining the next page data from paginated data
export const getNext = (next) => axiosInstance.get(next);

//obtaining the particular page data from paginated data
export const getPageUserGroup = (number, postsPerPage) =>
  axiosInstance.get(
    `api/v1/group-app/group?offset=${
      (number - 1) * postsPerPage
    }&limit=${postsPerPage}&ordering=-id`
  );

//creating function
export const createUserGroup = (body) =>
  axiosInstance.post(`api/v1/group-app/group`, body);



//update function
export const updateUserGroup = (id, body) =>
  axiosInstance.patch(`api/v1/group-app/group/${id}`, body);

//searching function
export const handleSearch = (search, postsPerPage) =>
  axiosInstance.get(
    `api/v1/group-app/group?offset=0&limit=${postsPerPage}&search=${search}`
  );
// checking the redundant data
export const checkRedundantData = (e, cancelToken) =>
  axiosInstance.get(`api/v1/group-app/group?name=${e.target.value}`, {
    cancelToken: cancelToken.token,
  });
//get All Permission
export const getAllPermission = () => {
  return axiosInstance.get(`api/v1/group-app/permission`);
};
//get all models
export const getAllPermissionCategory = () => {
  return axiosInstance.get(`api/v1/group-app/permission-category?ordering=-id`);
};
export const getSpecificPermissions = (id) =>
  axiosInstance.get(`api/v1/group-app/permission?category=${id}`);
